import {announceFromElement} from '@github-ui/aria-live'
import {controller, target} from '@github/catalyst'

@controller
export class FeedLiveContainerElement extends HTMLElement {
  @target container: HTMLElement

  connectedCallback() {
    // eslint-disable-next-line custom-elements/no-dom-traversal-in-connectedcallback
    const container = this.querySelector('.js-feed-live-region') as HTMLElement
    if (container) {
      announceFromElement(container)
    }
  }
}
