import {controller, attr} from '@github/catalyst'
import type {IFeedFilterValue} from './feed-filter-element'
import {FeedFilterElement} from './feed-filter-element'
import pickBy from 'lodash-es/pickBy'
import isEqual from 'lodash-es/isEqual'

export interface IFeedFilterNextValue extends IFeedFilterValue {
  RepositoryActivity: boolean
  StarredRelationships: boolean
}

@controller
export class FeedFilterNextElement extends FeedFilterElement {
  @attr default: string

  private get defaultValues(): IFeedFilterNextValue {
    return JSON.parse(this.default)
  }

  public get qualifiableFilters(): Array<keyof IFeedFilterNextValue> {
    return Object.keys(this.defaultValues).filter(k => k !== 'StarredRelationships') as Array<
      keyof IFeedFilterNextValue
    >
  }

  private get filterChangesCount(): number {
    const diff = pickBy(
      this.filterValues,
      (v, k) =>
        this.qualifiableFilters.includes(k as keyof IFeedFilterNextValue) &&
        isEqual(this.defaultValues[k as keyof IFeedFilterNextValue], v),
    )
    return Object.keys(diff).length
  }

  override connectedCallback() {
    this.updateFilterCounter()
  }

  public resetFilterToDefault() {
    const filter = this.defaultValues
    for (const input of this.inputs) {
      const name = input.getAttribute('name') as keyof IFeedFilterNextValue
      switch (input.type) {
        case 'checkbox':
          input.checked = this.defaultValues[name]
          break
        default:
          throw new Error(`Unexpected tag name: ${input.tagName}`)
      }
    }

    this.setFilterValue(filter)
  }

  public override async handleApply() {
    await super.handleApply()
    this.updateFilterCounter()
  }

  override async setFeedFilterSettings(previousTopic?: string | null): Promise<void> {
    await super.setFeedFilterSettings(previousTopic)
    this.updateFilterCounter()
  }

  private updateFilterCounter(): void {
    this.dispatchEvent(
      new CustomEvent('feed-filter-next:active-count', {
        bubbles: true,
        detail: {
          count: this.filterChangesCount,
          maximumFilterCapacity: this.qualifiableFilters.length,
        },
      }),
    )
  }
}
